<template>
  <div class="map_page" id="map_page">
    <div class="intro_part"></div>

    <div class="control_part">
      <div class="control_top_text">2021</div>
      <div class="control_top"></div>
      <div class="control_line"></div>
      <div class="control_ball"></div>
      <div class="control_bottom"></div>
      <div class="control_bottom_text">1995</div>
    </div>
    <div class="main_part">
      <div class="mask_top"></div>
      <div class="road_part">
        <img class="road" src="/static/map/road.png" />
      </div>
      <div class="line_short_part">
        <img
          class="yellow_line_short"
          src="/static/map/yellow_line_short.png"
        />
      </div>
      <div class="line_short_part_2">
        <img
          class="yellow_line_short_2"
          src="/static/map/yellow_line_short.png"
        />
      </div>
    </div>
      <div class="building" id="ani1995"></div>


    <LangTransfer></LangTransfer>
    <Menu></Menu>
  </div>
</template>

<style scoped lang="css">
::-webkit-scrollbar {
  display: none;
}

div.map_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}

div.intro_part {
}

div.main_part {
  position: absolute;
  z-index: 0;
  bottom: 0rem;
  height: 1400rem;
  width: 100%;
  perspective: 50rem;
}

div.building{
  width: 500rem;
  height: 1500rem;
}

div.control_part {
  position: fixed;
  height: 60%;
  bottom: 20%;
  left: 50rem;
  /* background: #000000; */
  width: 50rem;
  z-index: 4;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

div.control_top_text {
  position: absolute;
  font-size: 20rem;
  line-height: 20rem;
  margin-top: -20rem;
}

div.control_top {
  position: absolute;
  margin-top: 10rem;
  width: 10rem;
  height: 10rem;
  border-radius: 10rem;
  background: #251714;
}

div.control_line {
  width: 3rem;
  background: #251714;
  height: 60%;
}

div.control_ball {
  position: absolute;
  width: 30rem;
  height: 30rem;
  border-radius: 30rem;
  background: #efcd24;
}

div.control_bottom {
  position: absolute;
  bottom: 0rem;
  margin-bottom: 10rem;
  width: 10rem;
  height: 10rem;
  border-radius: 10rem;
  background: #251714;
}

div.mask_top {
  position: absolute;
  bottom: 700rem;
  width: 100%;
  height: 10000rem;
  background: #f7f7f7;
  z-index: 1;
}

div.road_part {
  display: flex;
  justify-content: center;
  transform: rotateX(60deg);
  transform-origin: bottom;
}

img.road {
  width: 1259rem;
  height: 1400rem;
}

div.line_short_part {
  display: flex;
  justify-content: center;
  transform: rotateX(60deg);
  transform-origin: bottom;
}

img.yellow_line_short {
  margin-top: -1400rem;
  width: 124rem;
  height: 1400rem;
}

div.line_short_part_2 {
  display: flex;
  justify-content: center;
  transform: rotateX(60deg);
  transform-origin: bottom;
}

img.yellow_line_short_2 {
  margin-top: -2800rem;
  width: 124rem;
  height: 1400rem;
}
</style>

<script>
// 实现方式通过anime.js中的keyframe + seek的方式实现，根据小球所在位置跳到对应的时间点
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";
import Footer from "../../components/footer/Footer";
import lottie from "../../../modules/lottie"

let line_short_anime; //马路运动的动画
//export default
export default {
  name: "Map",
  data() {
    return {
      global: global,
      total_distance: 0, //路程总距离
      past_distance: 0, //走过多距离
      per_distance: 100, //历史间的距离，单位rem
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer,
  },
  beforeCreate() {
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {
    let that = this;
    that.init();
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);

    line_short_anime = new anime({
      targets: ["img.yellow_line_short", "img.yellow_line_short_2"],
      translateY: ["0", "1400rem"],
      loop: true,
      easing: "linear",
      duration: 5000,
    });
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  destroyed() {},

  methods: {
    refresh_root_size() {
      let static_piexl = 0;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    init() {
      let anime1995 = lottie.loadAnimation({
        container: document.getElementById("ani1995") , // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: true,
        path: "/static/map/1995/1995.json", // the path to the animation json
        name: "1995"
      });
      console.log(anime1995)
    },
  },
};
</script>


